body {
  background-color: #10a2b4;
}
.App {
  display: grid;
  gap: 34px;
  padding: 34px;
  font-family: sans-serif;
  text-align: center;
}
.plan {
  border-radius: 14px;
  background-color: white;
  padding: 24px;
}
.plan:hover {
  transform: scale(1.01);
}
.plan:hover .plan-button {
  background-color: #0c7683;
}
.plan-button {
  width: 100%;
  border-radius: 24px;
  padding: 18px;
  margin: 2px;
  font-weight: bolder;
  background-color: #569afe;
  font-size: 18px;
  color: white;
  border: 1px solid white;
}
.plan-feature {
  list-style: none;
  padding-left: 34px;
  text-align: left;
}
.plan-feature li {
  padding-bottom: 16px;
}
.plan:first-child .plan-feature li:nth-child(n + 5) {
  opacity: 0.3;
}
.plan-feature li:before {
  content: "✔";

  margin-left: 20px;
}

.plan:first-child .plan-feature li:nth-child(n + 5):before {
  content: "✘";
}
.plan-head {
  font-weight: bolder;
  opacity: 0.3;
}
.plan-amount {
  font-weight: bolder;
  font-size: 45px;
}
.plan:nth-child(2) .plan-feature li:nth-child(n + 8):before {
  content: "✘";
}
.plan:nth-child(2) .plan-feature li:nth-child(n + 8) {
  opacity: 0.3;
}
@media (min-width: 740px) {
  .App {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    padding-top: 55px;
  }
}
